import React, {useEffect, useState} from "react";
import ProfileStatus from './ProfileStatus';
import {connect} from "react-redux";
import {getProfileStatus,updateProfileStatus} from '../../Redux/profile-reducer';

const mapStateToProps = (store) => {
    return {
        profileStatus: store.profileReducer.profileStatus
    }
};

const ProfileStatusContainer = (props)=> {
    const [statusToggle,setStatusToggle] = useState(false);

        const statusToggleSwitchHandler = () => {
          setStatusToggle(!statusToggle);
        };
        props.updateProfileStatus(props.profileStatus);
        return(
          <>
              <ProfileStatus
                  status = {props.profileStatus}
                  onStatusToggleSwitchHandler = {statusToggleSwitchHandler}
                  statusToggle = {statusToggle}
                  setProfileStatusHandler = {props.getProfileStatus}
              />
          </>
        )
    };

const ProfileStatusContainerConnect = connect(mapStateToProps,{getProfileStatus,updateProfileStatus})(ProfileStatusContainer);
export default ProfileStatusContainerConnect;
