import {dataAPI} from "./getData";
import {ContactsType} from "../Types/Types";
import {Dispatch} from "redux";
import {AppStateType} from "./redux-store";
import {ThunkAction} from "redux-thunk";

const FOLLOW = 'contacts/reducer/FOOLOW';
const UNFOLLOW = 'contacts/reducer/UNFOOLOW';
const SET_USERS = 'contacts/reducer/SET_USERS';
const CHANGE_PAGE = 'contacts/reducer/CHANGE_PAGE';
const USERS_COUNT = 'contacts/reducer/USERS_COUNT';
const FETCHING_DATA = 'contacts/reducer/FETCHING_DATA';
const TOGGLE_FOLLOW_BUTTON_TRUE = 'contacts/reducer/TOGGLE_FOLLOW_BUTTON_TRUE';
const TOGGLE_FOLLOW_BUTTON_FALSE = 'contacts/reducer/TOGGLE_FOLLOW_BUTTON_FALSE';

const initialState = {
    contacts: [] as Array<ContactsType>,
    followButtonId: false,
    pageSize: 5,
    currentPage: 1,
    totalCount: 0,
    fetchingData: false,
    toggleFollowButton: false

};

export type InitialStateType = typeof initialState // typeof берет значение переменнои

const contactsReducer = (state=initialState,action: ActionType):InitialStateType => {

    switch (action.type) {
        case FOLLOW:
            return {
                ...state,
                contacts: state.contacts.map(u => {
                    if (u.id === action.contactId) {
                        return{...u,followed :true}
                    }
                    return u;
                }),
            };
        case UNFOLLOW:
            return {
                ...state,
                contacts: state.contacts.map(u => {
                    if (u.id === action.contactId) {
                      return{...u,followed :false}
                    }
                    return u;
                }),
            };
        case SET_USERS:
            return {
                ...state,
                contacts: action.users
            };
        case USERS_COUNT:
            return {
                ...state,
                totalCount: action.totalCount
            };

        case CHANGE_PAGE:
            return {
                ...state,
                currentPage: action.pageNumber
            };
        case FETCHING_DATA:
            return {
                ...state,
                fetchingData: action.request
            };
        case TOGGLE_FOLLOW_BUTTON_TRUE:
            return {
                ...state,
                toggleFollowButton: true,
                followButtonId: action.toggleButton
            };
        case TOGGLE_FOLLOW_BUTTON_FALSE:
            return {
                ...state,
                toggleFollowButton: false,
                followButtonId: action.toggleButton
            };
        default:
            return state;
    }
};

type ActionType = followActionType | unfollowActionType | setUsersActionTypes | setChangePageActionType |
    setUsersTotalCountActionType | toggleFetchingDataActionType | toggleFollowButtonTrueActionType | toggleFollowButtonFalseActionType

type followActionType = {
    type: typeof FOLLOW
    contactId: number
}
export const follow =(contactId:number):followActionType=> ({type: FOLLOW,contactId });

type unfollowActionType = {
    type: typeof UNFOLLOW
    contactId: number
}
export const unfollow =(contactId:number):unfollowActionType=> ({type: UNFOLLOW,contactId });

type setUsersActionTypes = {
    type: typeof SET_USERS
    users: Array<ContactsType>
}
export const setUsers = (users:Array<ContactsType>):setUsersActionTypes => ({type:SET_USERS,users});

type setChangePageActionType = {
    type: typeof CHANGE_PAGE
    pageNumber: number
}
export const setChangePage = (pageNumber:number):setChangePageActionType => ({type:CHANGE_PAGE,pageNumber});

type setUsersTotalCountActionType = {
    type: typeof USERS_COUNT
    totalCount: number
}
export const setUsersTotalCount = (totalCount:number):setUsersTotalCountActionType => ({type: USERS_COUNT, totalCount});

type toggleFetchingDataActionType = {
    type: typeof FETCHING_DATA
    request: boolean
}
export const toggleFetchingData = (request:boolean):toggleFetchingDataActionType => ({type: FETCHING_DATA, request});

type toggleFollowButtonTrueActionType = {
    type: typeof TOGGLE_FOLLOW_BUTTON_TRUE
    toggleButton: boolean
    userId: number
}
export const toggleFollowButtonTrue = (toggleButton: boolean,userId: number):toggleFollowButtonTrueActionType => ({type: TOGGLE_FOLLOW_BUTTON_TRUE,toggleButton,userId});

type toggleFollowButtonFalseActionType = {
    type: typeof TOGGLE_FOLLOW_BUTTON_FALSE
    toggleButton: boolean
    userId: number
}
export const toggleFollowButtonFalse = (toggleButton: boolean,userId: number):toggleFollowButtonFalseActionType => ({type: TOGGLE_FOLLOW_BUTTON_FALSE,toggleButton,userId});

type DispatchType = Dispatch<ActionType>
type GetStateType = AppStateType
type ThunkYpe = ThunkAction<Promise<void>,AppStateType,unknown, ActionType>

export const getUsers = (currentPage: number,pageSize: number) => {
    return async (dispatch: DispatchType, getState: GetStateType) => {
        dispatch(toggleFetchingData(true));
        const response = await dataAPI.getData(currentPage,pageSize);
            dispatch(toggleFetchingData(false));
            dispatch(setUsers(response.items));
            dispatch(setUsersTotalCount(response.totalCount));
    }
};

export const changePage = (e:any,currentPage: number,pageSize:number) => {
    return async (dispatch: DispatchType, getState: GetStateType) => {
        dispatch(toggleFetchingData(true));
        const currentPage = +e.target.value;
        dispatch(setChangePage(currentPage));
        const response= await dataAPI.getData(currentPage,pageSize);
            dispatch(toggleFetchingData(false));
            dispatch(setUsers(response.items))
    }
};

export const followSuccess = (contactId:any) => {
    return async (dispatch: DispatchType, getState: GetStateType) => {
        dispatch(toggleFollowButtonTrue(true,contactId));
        const response= await dataAPI.postData(contactId);
          if (response.resultCode === 0) {
            dispatch(follow(contactId));
            dispatch(toggleFollowButtonFalse(false, contactId));

          }
    }
};

export const unFollowSuccess = (contactId:any) => {
    return async (dispatch: DispatchType, getState: GetStateType) => {
        dispatch(toggleFollowButtonTrue(true,contactId));
        const response = await dataAPI.deleteData(contactId);
            if (response.resultCode === 0) {
                dispatch(unfollow(contactId));
                dispatch(toggleFollowButtonFalse(false, contactId));
            }
    }
};

export default contactsReducer;
