import React from "react";
import {Field, reduxForm} from "redux-form";
import Textarea from "./Textarea/Textarea";
import {required} from "./validateChecker";
import Button from "./Button/button";
import createField from './createField';
import {Input} from "./Input/Input";

const WallReduxFormPost = (props) => {
    return(
        <div>
            <form onSubmit={props.handleSubmit}>
                {createField('inp',
                    [required],
                    Input,
                    'Enter your name',
                    {type: 'text',}
                )}
                {createField('wallPost',[required],Textarea,'Enter Your Message',)}
                <Button classNames='button'/>
            </form>
        </div>
    )
};


export default reduxForm({form: 'profileWallPost'})(WallReduxFormPost);