import React from "react";
import {skillsType} from "../../../Types/Types";

type PropsType = {
    skills: Array<skillsType>
}

const ProfileSkills:React.FC<PropsType> = ({skills}) => {
    return(
        <div className='skills'>
            <div>Skills <hr/></div>
            <div>
                <nav>
                    <ul>
                        {
                          skills.map((skill)=> {
                              return <li key={skill.id}>{skill.skill}</li>
                          })
                        }
                    </ul>
                </nav>
            </div>
        </div>
    )
};

export default ProfileSkills;
