import React from "react";
import Header from "./Header";
import {connect} from "react-redux";
import {setAuthState,getAuth,logoutExample} from "../../Redux/auth-reducer";

class HeaderContainer extends React.Component{
    componentDidMount() {
        this.props.getAuth()
    }

    render() {
       return (
           <>
               <Header props={this.props} logoutExample ={this.props.logoutExample}/>
           </>
           )
    }
}
const mapStateToProps = (store) => {
    return {
        id: store.authReducer.Id,
        login: store.authReducer.login,
        email: store.authReducer.email,
        isAuth: store.authReducer.isAuth
    }
};
const HeaderApiContainer = connect(mapStateToProps,{setAuthState,getAuth,logoutExample})(HeaderContainer);
export default HeaderApiContainer;
