import React from "react";
import {NavLink,Redirect} from "react-router-dom";

const contacts = (props) => {
    const pageCount = Math.ceil( props.totalCount / props.pageSize);
    let page = [ ];
    for (let i=1; i <= pageCount; i++) {
        page.push(i);
    }
    return(
        <nav>
            <ul className= 'contacts-wrap'>
                {
                    props.contacts.map((contact) => {
                        return(
                            <li className= 'my-contacts' key={contact.id}>
                                <NavLink to={`/profile/${contact.id}`}>
                                    {contact.name}
                                </NavLink>
                                <div>{!contact.followed ?
                                    <button disabled={props.followButtonId === contact.id ? props.toggleFollowButton: null}
                                            onClick={() => props.followSuccess(contact.id)}
                                     >follow</button>
                                    : <button disabled={props.followButtonId === contact.id ? props.toggleFollowButton: null}
                                                onClick={()=> props.unFollowSuccess(contact.id)}
                                    >unfollow</button>}
                                </div>
                            </li>
                        )
                    })
                }
            </ul>
            <div className='pageinaton-wrap'>
                {
                    page.map((p,index) => <button
                        onClick={props.changePage}
                        key= {index}
                        className={props.currentPage === p && `curent-page`}
                        value= {index+1}

                    >
                        {p}
                    </button>)
                }
            </div>
        </nav>
    )
};

export default contacts;
