import React, {useContext, useEffect, useState} from "react";
import {connect} from "react-redux";
import EditProfilePage from './EditProfilePage';
import {getProfileName} from  '../../Redux/profile-selector';
import {updateProfileInformation} from '../../Redux/profile-reducer';

const mapStateToProps = (store) => {
    return {
        profileName: getProfileName(store),
        initialValues: store.profileReducer
    }
};

const SettingsContainerComponent = ({updateProfileInformation}) =>{

    const onSubmit = (updatedInformation) => {
        updateProfileInformation(updatedInformation);
    };

    return(
            <div>
                <EditProfilePage onSubmit = {onSubmit}  initialValues/>
            </div>
    )
};

export const SettingsConnectComponent = connect(mapStateToProps,{updateProfileInformation})(SettingsContainerComponent);