import * as axios from "axios";

// THIS IS OUR DAL STORE

const instance = axios.create({
    baseURL: 'https://social-network.samuraijs.com/api/1.0/',
    withCredentials:true,
    headers: {
        'API-KEY': 'ac941127-512b-43be-8974-91e0c7818dbb'
    }
});

export const dataAPI = {
    getData(currentPage, pageSize) {
        return instance.get(`users?page=${currentPage}&count=${pageSize}`
        ).then(response => response.data)
    },

    postData(contactId) {
        return instance.post(`follow/${contactId}`).then(response => response.data)
    },

    deleteData(contactId) {
        return instance.delete(`follow/${contactId}`).then(response => response.data)
    },

    getProfileData(userId) {
        console.warn('backup responsibility, you use old version');
        return profileData.getProfileData(userId)
    }
};

export const authData = {
    getAuthData() {
       return instance.get(`auth/me`).then(response => response.data)
    },

    login(email,password,rememberMe) {
        return instance.post('auth/login',{email,password,rememberMe}).then(response => response.data)
    },

    logout() {
        return instance.delete('auth/login').then(response => response.data)
    }
};

export const profileData = {
    // всегда смотри куда ты отпровляеш данные на какои адресс
    getProfileStatus(userId) {
        return instance.get(`profile/status/${userId}`).then(response => response.data)
    },

    setProfileStatus(status) {
        return instance.put(`profile/status`,{status}).then(response => response.data)
    },

    setProfileInformation(updatedInformation) {
        return instance.put(`profile`, updatedInformation).then(response => response.data)
    },

    setProfilePhoto(photos) {
        const formData = new FormData();
        formData.append('image',photos);

        return instance.put(`profile/photo`, formData, {
            headers: {
                'Content-Type': 'multipart/form-data'
            }
        }).then(response => response.data)
    },

    getProfileData(userId) {
        return instance.get(`profile/${userId}`).then(response => {
            console.log(response.data,'waiting response')
            return response.data
        })
    },
};

