import React from "react";
import './HeaderSearchInputComponent.scss';
import createField from "../createField";
import {required} from "../validateChecker";
import {Input} from "../Input/Input";
import {reduxForm} from 'redux-form';

const HeaderSearchInputComponent = () => {
    return(
        <div className='search-input'>
          {createField('search-inp',[required],Input,`Search`,{type:'text'})}
        </div>
    )
};

export default reduxForm({form: 'headerSearchIput'})(HeaderSearchInputComponent);