import React from "react";
import WallReduxFormPost from './WallPostReduxForm';

const ProfileWall = (props) => {
    return(
        <div className='my-wall'>
            <div className="post-place">
                <ul>
                    {
                        props.contactMessage.map((message,index) => {
                            return (
                                <li key = {index}>{message.messageToMe}</li>
                            )
                        })
                    }
                </ul>
            </div>
            <WallReduxFormPost
                onSubmit={(e)=>props.onPushNewMessage(e.wallPost)}
                value={props.myLastMessage}
            />
        </div>
    )
};

export default ProfileWall;
