import React from "react";
import {connect} from "react-redux";
import {
    setProfileInfo,
    getProfile,
    getProfileStatusApi,
    profileInfoType
} from '../../Redux/profile-reducer';
import {withRouter} from 'react-router-dom';
import ProfileImg from "./Profile img/ProfileImgContainer";
import WorkHistory from "./ProfileWorkHistory/ProfileWorkHistory";
import ProfileSkills from "./ProfileSkills/ProfileSkills";
import {ProfileNameAndCity} from "./Profile Name & City/ProfileNameAndCity";
import {ProfileJobStatus} from "./ProfileJobStarus/ProfileJobStarus";
import {ProfileRating} from "./Profile Rating/ProfileRating";
import {ProfileCommunication} from "./ProfileCommunication/ProfileCommunication";
import {ProfileInfo} from "./Profile Info/InfoAboutProfile";
import {getProfileName} from '../../Redux/profile-selector';
import ProfileStatusContainerConnect from "./ProfileStatusContainer";
import {AppStateType} from "../../Redux/redux-store";
import {profileContactsInformationType} from "../../Types/Types";

type mapStateToPropsType = {
    profileInfo: Array<profileInfoType>
    profileName: string
    lookingForAJob: boolean
    jobPosition: string
    skills: Array<string>
    profileContactsInformation: Array<profileContactsInformationType>
    basicInformation: string
}

type mapDispatchToPropsType = {
    getProfile: (userId: number) => void
    setProfileInfo: ()=> void
    getProfileStatusApi: (userId:number)=> void
}

type MyStateType = {
    match: any
    userId: any
}

type PropsType = any
// type PropsType = mapStateToPropsType & mapDispatchToPropsType & MyStateType

const mapStateToProps = (store:AppStateType) => {
    return {
        profileInfo: store.profileReducer.profileInfo,
        profileName: getProfileName(store),
        lookingForAJob: store.profileReducer.lookingForAJob,
        jobPosition: store.profileReducer.jobPosition,
        skills: store.profileReducer.skills,
        profileContactsInformation: store.profileReducer.profileContactsInformation,
        basicInformation: store.profileReducer.basicInformation,

    }
};

class ProfileContainer extends React.Component<PropsType> {

    componentDidMount() {
        const userId = this.props.match.params.userId || 5957;
        this.props.getProfile(userId);
        this.props.getProfileStatusApi(userId)
    }

    render() {
        console.log('THIS PROPS TEST TEST', this.props)
        return (
            <div>
                <div className= 'profile'>
                    <div className='left-block'>
                        <ProfileImg/>
                        <ProfileStatusContainerConnect/>
                        <WorkHistory/>
                        <ProfileSkills skills={this.props.skills}/>`
                    </div>

                    <div className='right-block'>
                        <ProfileNameAndCity profileName={this.props.profileName}/>
                        {/*<ProfileJobStatus*/}
                        {/*    lookingForAJob={this.props.lookingForAJob}*/}
                        {/*    jobPosition={this.props.jobPosition}*/}
                        {/*/>*/}
                        <ProfileRating/>
                        <ProfileCommunication/>
                        <ProfileInfo profileContactsInformation={this.props.profileContactsInformation}
                                     basicInformation={this.props.basicInformation}
                        />
                    </div>
                    {/*  <div className="font-weight-bolder">`{props.profileInfo.fullName}`</div>*/}
                    {/*  <ProfileStatusContainer/>*/}
                </div>
                {/*{!this.props.profileInfo ? <Preloader/>*/}
                {/*    : <Profile profileInfo={this.props.profileInfo}/>*/}
                {/*}*/}
            </div>
        )
    }
}

const WithRouterComponent = withRouter(ProfileContainer);

const ProfileApiContainer = connect(mapStateToProps, {setProfileInfo,getProfile,getProfileStatusApi})(WithRouterComponent);
export default ProfileApiContainer;