import React from "react";
import {pushMessage} from "../../Redux/profile-reducer";
import TextareaComponent from "./TextareaComponent";
import {connect} from "react-redux";

let mapStateToProps = (store) => {
    return {
        myLastPost : store.profileReducer.myLastPost,
    }
};

const  TextareaComponentContainer = connect(mapStateToProps,{pushMessage})(TextareaComponent);
export default TextareaComponentContainer;