import React from "react";

export const required = value => (value || typeof value === 'number' ? undefined : 'Field is required');

export const maxValue = max => {
    return (value) => {
        if (value.length > max) {
            return `Maximum value length must be ${max} characters or less`;
        }
    }
};

export const minValue = min => {
    return (value) => {
        if (value.length < min) {
            return `Minimum value length must be ${min} characters or more`;
        }
    }
};