import React from "react";

const MyPosts = ({myProfileName,myText}) => {
    return (
        <div className= 'posts-container'>
            {
                myText.map((text,index) => {
                    return(
                        <div key ={index}>
                            <h5>{myProfileName}</h5> ---<span>{text.text}</span>
                        </div>
                    )
                })
            }
        </div>
)
};
export default MyPosts;