import {authData} from "./getData";
import {stopSubmit} from "redux-form";
import {Dispatch} from "redux";
import {AppStateType} from "./redux-store";

const SET_AUTH_STATE = 'auth/reducer/SET_AUTH_STATE';
const LOGOUT = 'auth/reducer/LOGOUT';

let initialState = {
    Id : null as number | null,
    login: null as string | null,
    email: null as string | null,
    isAuth: false
};

export type InitialStateType = typeof initialState; // typeof берет содержимое переменнои

const authReducer = (state=initialState,action: ActionType): InitialStateType=> {
    switch (action.type) {
        case SET_AUTH_STATE:
            return {
                ...state,
                ...action.payload,
                isAuth: true
                // тут имя елементов одни и те же и поетому то чро с низу перезатирет то что с верху
            };
        case LOGOUT :
            return {
              ...state,
              ...action.payload
            };
        default:
            return state
    }
};

type ActionType = SetAuthStateActionType | LogoutActionType

type SetAuthStateActionPayloadType = {
    Id: number | null
    login: string | null
    email: string | null
    isAuth: boolean
}

type SetAuthStateActionType = {
    type: typeof SET_AUTH_STATE, // typeof он берет значение переменнои 
    payload: SetAuthStateActionPayloadType
}

export const setAuthState = (Id: number | null, login: string | null, email: string | null,isAuth:boolean): SetAuthStateActionType =>
    ({type: SET_AUTH_STATE, payload: {Id,login,email,isAuth}});

type LogoutActionType = {
    type: typeof LOGOUT
    payload: SetAuthStateActionPayloadType
}

export const logoutAC = (Id: number|null, login: string|null, email: string|null, isAuth:boolean):LogoutActionType =>
    ({type:LOGOUT, payload:{Id,login,email,isAuth}});

type DispatchType = Dispatch<ActionType>
type GetStateType = AppStateType

export const getAuth = () => {
    console.log('getAuth is Working');
    return async (dispatch:DispatchType, getState: GetStateType) => {
        const response = await authData.getAuthData();
            if (response.resultCode === 0) {
                const {id, login, email} = response.data;
                dispatch(setAuthState(id, login, email,false));
            }
    }
};

export const loginExample = (email:number|null,password:number|null,rememberMe:boolean) => {
    return async (dispatch:DispatchType, getState: GetStateType) => {
        const response = await authData.login(email,password,rememberMe);
        if (response.resultCode === 0) {
            console.log(response.resultCode);
            getAuth();
        }
        else {
            let messages = response.messages[0];
            stopSubmit('login', {email: messages, password: messages})
        }
    }
};

export const logoutExample =() => {
    return async (dispatch:DispatchType, getState: GetStateType) => {
        const response = await authData.logout();
            if (response.resultCode === 0) {
                dispatch(logoutAC(null, null, null, false))
            }
    }
};

export default authReducer;