import React from "react";
import './Input.scss';

export const Input = ({input,meta,placeholder,type,values}) => {
    return(
      <div>
                <div className='input-component'>
                  <input {...input} type={type} value={values} placeholder={placeholder} className={meta.touched && meta.error && 'error-border'}/>
                  {meta.touched && meta.error && <div className='error-text'>{meta.error}</div>}
                </div>
      </div>
    )
};