import React from "react";
import {reduxForm,Field} from 'redux-form';
import {required,maxValue} from './validateChecker';
import Textarea from "./Textarea/Textarea";
import Button from "./Button/button";

const maxValue12 = maxValue(20);
const PostReduxForm = (props) => {

    return (
        <div>
            <form onSubmit={props.handleSubmit}>
                <Field
                    validate={[required,maxValue12]}
                    name='createPost'
                    component = {Textarea}
                    id="txt-area" cols="20" rows="4"
                    placeholder='Enter Your message'
                    value={props.myLastPost}
                />
                <Button/>
            </form>
        </div>
    )
};

export default reduxForm({form: 'profile-create-post'})(PostReduxForm);