import {profileData} from "./getData";
import {stopSubmit} from "redux-form";
import {myMessageType, myPostType, UsersPhotosType,skillsType,profileContactsInformationType} from "../Types/Types";
import {Dispatch} from "redux";
import {AppStateType} from "./redux-store";

const PUSH_NEW_POST = 'profile/reducer/PUSH_NEW_POST';
const SET_PROFILE_INFO = 'profile/reducer/SET_PROFILE_INFO';
const GET_PROFILE_STATUS = 'profile/reducer/GET_PROFILE_STATUS';
const UPDATE_PROFILE_PHOTO = 'profile/reducer/UPDATE_PROFILE_PHOTO';

export type profileInfoType = {
    userId: number
    fullName: string
    lookingForAJob: boolean
    lookingForAJobDescription: string | null
    photos : Array<UsersPhotosType> | null
    basicInformation: object
}

let initialState = {
    profileName: 'test' as string | null,
    profileInfo: [] as Array<profileInfoType> | null,
    profileStatus:  null as string | null,
    lookingForAJob: false as boolean | null,
    jobPosition: 'JavaScript Developer' as string | null,
    profilePhoto : [{large: null,small: null}] as Array<UsersPhotosType> | null,

    myPost: [
     {id: 1, text: 'hey', likeCount: 1},
     {id: 2, text: 'god you ?', likeCount: 2},
     {id: 3, text: 'ur ok', likeCount: 3},
     {id: 4, text: 'heys', likeCount: 4},
    ] as Array<myPostType>,

    myMessage: [
     {id: 1, text: 'hey', likeCount: 1},
     {id: 2, text: 'god you ?', likeCount: 2},
     {id: 3, text: 'ur ok', likeCount: 3},
     {id: 4, text: 'heys', likeCount: 4},
    ] as Array<myMessageType>,

    skills: [
     {id:1, skill: 'HTML'},
     {id:2, skill: 'CSS'},
     {id:3, skill: 'JAVASCRIPT'},
     {id:4, skill: 'REACT'},
    ] as Array<skillsType>,

    profileContactsInformation: [
     {id:1, key:'Phone:', value:'+1(324) 000 0000'},
     {id:2, key:'Adress:', value:'528 E 67th street NY NEW YORK BROOKLN REDHOOK'},
     {id:3, key:'E-mail:', value:'example@mail.ru'},
     {id:4, key:'Site:', value:'www.example.com'},
    ] as Array<profileContactsInformationType>,

    basicInformation: {
     birthday:'June 9 1996',
     gender: 'Male'
    },
};

export type InitialStateType = typeof initialState
const profileReducer = (state = initialState,action: ActionType):InitialStateType => {
    switch (action.type) {
        case PUSH_NEW_POST : {
            return {
                ...state,
                myMessage: [...state.myMessage,{id: 7,likeCount: 0,text: action.message}]
            }
        }

        case SET_PROFILE_INFO: {
            return {
                ...state,
                ...state.profilePhoto,
                profilePhoto: action.profileInfo.photos,
                profileName: action.profileInfo.fullName,
                lookingForAJob: action.profileInfo.lookingForAJob,

            }

        }

        case UPDATE_PROFILE_PHOTO: {
            return {
                ...state,
                ...state.profilePhoto,
                profilePhoto: action.profilePhoto
            }
        }

        case GET_PROFILE_STATUS: {
            return {
                ...state,
                profileStatus: action.profileStatus
            }
        }

        default :
            return state;
    }
};

export default profileReducer;

type ActionType = getProfileStatusActionType | setProfileInfoActionType | updateProfilePhotoActionType | pushMessageActionType

type getProfileStatusActionType = {
    type: typeof GET_PROFILE_STATUS
    profileStatus: string
}
export const getProfileStatus = (profileStatus: string):getProfileStatusActionType => ({type: GET_PROFILE_STATUS, profileStatus});

type setProfileInfoActionType = {
    type: typeof SET_PROFILE_INFO
    profileInfo: profileInfoType
}
export const setProfileInfo = (profileInfo : profileInfoType):setProfileInfoActionType => ({type: SET_PROFILE_INFO, profileInfo});

type updateProfilePhotoActionType = {
    type: typeof UPDATE_PROFILE_PHOTO
    profilePhoto: Array<UsersPhotosType>
}
export const updateProfilePhoto = (profilePhoto: Array<UsersPhotosType>):updateProfilePhotoActionType => ({type: UPDATE_PROFILE_PHOTO, profilePhoto});

type pushMessageActionType = {
    type: typeof PUSH_NEW_POST
    message: string
}
export const pushMessage = (message:string):pushMessageActionType => ({type : PUSH_NEW_POST,message});

type DispatchType = Dispatch<ActionType>
type GetStateType = AppStateType

export const getProfile = (userId:number) => {
    return async (dispatch: DispatchType, getState: GetStateType) => {
        const response = await profileData.getProfileData(userId);
        dispatch(setProfileInfo(response))
    }
};

export const getProfileStatusApi = (userId:number) => {
    return async (dispatch: DispatchType, getState: GetStateType) => {
        const response = await profileData.getProfileStatus(userId);
            dispatch(getProfileStatus(response))
    }
};

export const updateProfileStatus = (status:string) => {
    return () => {
        profileData.setProfileStatus(status);
    }
};
// в диспатче делаем диспатч откуда хотим
export const addProfilePhoto = (profilePhoto:Array<UsersPhotosType>) => {
    return async (dispatch: DispatchType, getState: GetStateType) => {
        const response = await profileData.setProfilePhoto(profilePhoto);
        if (response.resultCode === 0) {
            dispatch(updateProfilePhoto(response.data.photos))
        }else {
            let messages = response.messages[0];
            stopSubmit('user-photo', {photos: messages})
        }
    }
};

export const updateProfileInformation = (updatedInfromation:any) => {
    return async () => {
      await profileData.setProfileInformation(updatedInfromation);
    }
};



