import React from "react";
import './EditFormInputs.scss';
import createField from "../createField";
import {required} from "../validateChecker";
import {Input} from "../Input/Input";
import {reduxForm} from "redux-form";

const EditFormInputs = ({profileName}) => {
    return(
       <div>
           <div>
               <span>Name</span>
               {createField('edit-name',[required],Input,'',null,'',{type:'text',values: profileName})}
           </div>
       </div>
    )
};

export default reduxForm({form: 'editFormInput'})(EditFormInputs);