import React from "react";
import {connect} from "react-redux";
import {getUsersSelector,getPageSizeSelector,getCurrentPageSelector,
        getTotalCountSelector,getFetchingDataSelector,getToggleFollowButtonSelector,
        getFollowButtonIdSelector} from '../Redux/contacts-selector';

import {setUsers,setChangePage,setUsersTotalCount,
        toggleFetchingData,toggleFollowButtonTrue,toggleFollowButtonFalse,
        getUsers,changePage,followSuccess,unFollowSuccess} from '../Redux/contacts-reducer';
import Contacts from './contacts';
import Preloader from "./Preloader";
import {AppStateType} from '../Redux/redux-store';
import {ContactsType} from "../Types/Types";

type mapStateToPropsType = {
    contacts: Array<ContactsType>
    pageSize: number
    currentPage: number
    totalCount: number
    fetchingData: boolean
    toggleFollowButton: any
    followButtonId: number
}

type mapDispatchToPropsType = {
    getUsers: (currentPage: number, pageSize: number) => void
    followSuccess: (contactId: number) => void
    unFollowSuccess: (contactId: number) => void
    setUsers: (users: Array<ContactsType>) => void
    setChangePage: (pageNumber:number) => void
    setUsersTotalCount: (totalCount:number) => void
    toggleFetchingData: (request:boolean) => void
    toggleFollowButtonTrue: (toggleButton: boolean,userId: number) => void
    toggleFollowButtonFalse: (toggleButton: boolean,userId: number) => void
    changePage: (e:any,currentPage: number,pageSize:number) => void
}

type PropsType = mapDispatchToPropsType & mapStateToPropsType

const mapStateToProps = (store:AppStateType) => {
    return {
        contacts: getUsersSelector(store),
        pageSize: getPageSizeSelector(store),
        currentPage: getCurrentPageSelector(store),
        totalCount: getTotalCountSelector(store),
        fetchingData: getFetchingDataSelector(store),
        toggleFollowButton: getToggleFollowButtonSelector(store),
        followButtonId: getFollowButtonIdSelector(store),
    }
};


class ContactsApiContainer extends React.Component<PropsType>{

    componentDidMount() {
        const {currentPage,pageSize} = this.props;
        this.props.getUsers(currentPage,pageSize)
    };

    render(){
        console.log(this.props, 'Props');
        return(
         <>
             {
                this.props.fetchingData ?
                 <Preloader/>
                 : < Contacts
                     followSuccess ={this.props.followSuccess}
                     unFollowSuccess ={this.props.unFollowSuccess}
                     toggleFollowButton = {this.props.toggleFollowButton}
                     changePage = {(e :any)=>this.props.changePage(e,this.props.currentPage,this.props.pageSize)}
                     totalCount = {this.props.totalCount}
                     pageSize = {this.props.pageSize}
                     contacts = {this.props.contacts}
                     currentPage = {this.props.currentPage}
                     toggleFollowButtonTrue = {this.props.toggleFollowButtonTrue}
                     toggleFollowButtonFalse = {this.props.toggleFollowButtonFalse}
                     followButtonId = {this.props.followButtonId}
                     // isAuth = {this.props.isAuth}
                 />
             }
         </>
        )
    }
}

const ContactsContainer = connect(mapStateToProps,{
    setUsers,
    setChangePage,
    setUsersTotalCount,
    toggleFetchingData,
    toggleFollowButtonTrue,
    toggleFollowButtonFalse,
    getUsers,
    changePage,
    followSuccess,
    unFollowSuccess
})(ContactsApiContainer);
export default ContactsContainer;