const PUSH_NEW_MESSAGE = 'PUSH_NEW_MESSAGE';

let initialState = {
    contactMessage: [
        {messageToMe: 'Hey sup?'},
        {messageToMe: 'ok'},
        {messageToMe: 'yeap'},
        {messageToMe: 'hehe'},
        {messageToMe: 'nooooo'}
    ]
};

const dialogsReducer = (state= initialState,action) => {
    switch (action.type) {
        case PUSH_NEW_MESSAGE :
            return {
                ...state,
                contactMessage: [...state.contactMessage,{id: 7,likeCount: 2,messageToMe: action.e}],
            };

        default :
            return state;
    }
};

export const onPushNewMessage = (e) => ({type : PUSH_NEW_MESSAGE, e});
export default dialogsReducer;
