import React from "react";
import { Field, reduxForm } from 'redux-form';
import {maxValue, required} from "./validateChecker";
import createField from "./createField";
import {Input} from "./Input/Input";
const maxValue12 = maxValue(30);
const maxValue24 = maxValue(24);

class LoginForm extends React.Component{
    componentDidUpdate(prevProps, prevState, snapshot) {
        
    }

    render() {
        return(
           <div>
                   <div>
                           <form onSubmit={this.props.handleSubmit}>
                               Enter Your Login {createField("email",[required,maxValue24],Input,'Enter Your Login',null,'e-mail',{type: 'text'})}
                               Enter Your Password {createField("password",[required],Input,'Enter Your Login',null,'Password',{type: 'password'})}
                               <Field name="rememberMe" component="input" type="checkbox"/>
                               <button>LOGIN</button>
                           </form>
                   </div>
           </div>
        )
    }
}
const LoginReduxForm =reduxForm({form: 'login'})(LoginForm);
export default LoginReduxForm;
