import React, {useEffect, useState} from "react";
import LoginForm from './LoginForm';
import './login.scss';
import {connect} from "react-redux";
import {loginExample} from '../Redux/auth-reducer';
import {Redirect} from 'react-router-dom';

const LoginContainer = (props) => {

    const onSubmit = (formData) => {
        props.loginExample(formData.email,formData.password,formData.rememberMe,formData.isAuth);
    };

    return (
      <>
          <div className='login'>
                  <h1>You have to login</h1>
                  <LoginForm
                      onSubmit={onSubmit}
                      isAuth = {props.isAuth}
                      email={props.email}
                  />
              </div>
      </>
    )
};
export default connect(null,{loginExample})(LoginContainer);