import React from "react";

const ProfileStatus = (props) => {
    console.log(props,'ProfileStatus')
    return(
        <div className='profile-status'>
            {
             props.statusToggle ? <input type="text"
                                         value={props.status}
                                         onClick={props.onStatusToggleSwitchHandler}
                                         onBlur={props.onStatusToggleSwitchHandler}
                                         autoFocus
                                         onChange={(e)=>props.setProfileStatusHandler(e.target.value)}
                 />
             : <div className='status-div' onClick={props.onStatusToggleSwitchHandler}>{props.status || `No Status Yet`}</div>
            }
        </div>
    )
};

export default ProfileStatus;



