import React from "react";
import {onPushNewMessage} from "../Redux/dialogs-reducer";
import ProfileWall from './myProfileWall';
import {connect} from "react-redux";
import isAuthReducerComponent from "./withRouterComponent";
import {getMyMessage,getMyLastMessage,getContactMessage,getIsAuth} from '../Redux/profile-selector';

const mapStateToProps = (store) => {
    return {
        myMessage: getMyMessage(store),
        myLastMessage: getMyLastMessage(store),
        contactMessage : getContactMessage(store),
        isAuth: getIsAuth(store)
    }
};
const ProfileWallContainer = connect(mapStateToProps,{onPushNewMessage})(ProfileWall);
export default isAuthReducerComponent(ProfileWallContainer);